export const videos = [{
    '晴': 'https://pic.ibaotu.com/00/43/77/65N888piCeBG.mp4',
    '云': 'https://pic.ibaotu.com/00/45/70/63j888piCJXc.mp4',
    '雨': 'https://pic.ibaotu.com/00/45/64/32S888piCdw2.mp4',
    '雷': 'https://pic.ibaotu.com/00/44/73/49C888piCs7D.mp4',
    '雪': 'https://pic.ibaotu.com/00/45/61/63r888piCdk7.mp4'
}];

class Weather{
    constructor(){

    }

    formtData(data){

        //要匹配的格式
        var rdata = {
            '晴': 'w1.png',
            '云': 'w2.png',
            '雨': 'w3.png',
            '雷': 'w4.png',
            '雪': 'w5.png',
            '无': 'w6.png'
        };

        var vdata = {
            '晴': 'https://pic.ibaotu.com/00/43/77/65N888piCeBG.mp4',
            '云': 'https://pic.ibaotu.com/00/45/70/63j888piCJXc.mp4',
            '雨': 'https://pic.ibaotu.com/00/45/64/32S888piCdw2.mp4',
            '雷': 'https://pic.ibaotu.com/00/44/73/49C888piCs7D.mp4',
            '雪': 'https://pic.ibaotu.com/00/45/61/63r888piCdk7.mp4'
        };

        var wdata = {
            '一': 'MON',
            '二': 'TUE',
            '三': 'WED',
            '四': 'THU',
            '五': 'FRI',
            '六': 'SAT',
            '日': 'SUN'
        }


        var tem = /\d+\.*\d*/g;
        var nData = [];
        var week = '';
        var img = '';
        var video = '';


        //确定星期
        for(var i=0;i<data.length;i++){

            var high = data[i].high.match(tem)*1;
            var low = data[i].low.match(tem)*1;
            var text = Math.ceil((high+low)/2) + '°C';

            //星期
            for(var key in wdata){
                if(data[i].date.match(key)){
                    week = wdata[key];
                    break;
                }
            }

            //天气
            for(var key in rdata){
                if(data[i].type.match(key)){
                    img = rdata[key];
                    break;
                }
            }

            //视频
            for(var key in vdata){
                if(data[i].type.match(key)){
                    video = vdata[key];
                    break;
                }
            }

            nData.push({
                'week': week,
                'img': img,
                'text': text,
                'video': video
            });
        }

        var d ={
            tem: nData[0].text,
            img: nData[0].img,
            week: nData[0].week,
            day: new Date().getDate()+'TH',
            wind: data[0].fx.length>4 ? data[0].fx.substr(0,4) : data[0].fx,
            windBounce: data[1].fl,
            video: nData[0].video
        };

        var sweather = {
            text: data[0].type,
            temperature: data[0].high
        };

        return {
            c: d,
            f: nData,
            s: sweather
        };
    }

    addZoom(num){
        if(num*1>=10){
            return num;
        }else{
            return '0'+num;
        }
    }

    //格式化时间
    formatTime(){
        var date = new Date();
        var hour = date.getHours();
        var minute = date.getMinutes();
        var noons = hour>=12 ? 'pm': 'am';
        
        return this.addZoom(hour)+':'+this.addZoom(minute) +' '+ noons;
    }
  
}

export default new Weather();


