<template>
    <div class="conainer-weather dragBox" id="conainer-weather" ref="wcontainer">
        
        
        <div class="weather-controls">
            <div class="wh-area">{{areaInfo.city}}</div>
            <div class="wh-area-details">
                <span class="wad ad-icon" @click.stop="scaleWeather"></span>
                <span class="wad ad-country">{{areaInfo.country}}</span>
                <span class="wad ad-time">{{areaInfo.time}}</span>
                <span :class="show?'wad ad-weather wadActive':'wad ad-weather'" >{{sweather.text}}</span>
                <span :class="show?'wad ad-temperature wadActive':'wad ad-temperature '">{{sweather.temperature}}</span>
            </div>
            <div class="wh-change-area">
                <span class="fontBox">NEXT ONE</span>
            </div>

            <div class="wh-prompt">
                <ul class="wp-list-wrap">
                    <li class="wli">各类人群可自由活动</li>
                    <li class="wli">
                        <span class="li-pm">PM10: 11</span>
                        <span class="li-pm">PM2.5: 7</span>
                    </li>
                    <li class="wli">空气质量: 优 | 湿度: 98%</li>
                    <li class="wli">出门最好穿雨衣，勿挡视线</li>
                    <li class="wli">各类人群可自由活动</li>
                </ul>
            </div>

            <div class="wh-card-info" >
                <span class="wci weather-info">WEATHER</span>
                <span class="wci news-info">NEWS & EVENT</span>
                <span class="wci gallery-info">GALLERY(05)</span>
            </div>
            <!-- 轮播滚动进度条 -->
            <div class="wh-progress-lines">
                <ul class="listWrap">
                    <li v-for="item,index in sliders" :class=" index==slidersIndex ? 'lineItem lineItemActive' : 'lineItem' "></li>
                </ul>
            </div>
        </div>
        

        <section class="w-header">
            <div class="w-header-box">
                <video id="videos" loop v-show="videoShow" >
                    <source v-bind:src="currentWeather.video"  type="video/mp4" />
                </video>
                <img :src="require('./images/'+sliders[slidersIndex])" class="bgImg" v-show="!videoShow" />
                <div class="sliders" id="wsliders">
                    <!-- <img :src="require('./images/'+sliders[slidersIndex])" class="bgImg" /> -->
                    <canvas id="weather-canvas" width='500' height="228"></canvas>
                </div>
                
            </div>
        </section><!--// end w-header -->

        <section class="w-body">
            <div class="wb-bg-color"></div>     
            <div class="wb-current-weather">
                <div class="wcw-tem">
                    <span class="wcw-tem-value">{{currentWeather.tem}}</span>
                    <span class="wcw-tem-week">
                        <i class="wyv-week">{{currentWeather.week}}</i>
                        <i class="wyv-day">{{currentWeather.day}}</i>
                    </span>
                </div>
                <div class="wcw-icon">
                    <span class="wcw-icon-value">
                        <img :src="require('./images/'+currentWeather.img)" />
                    </span>
                    <span class="wcw-icon-wind">
                        <i class="wiv-wind">{{currentWeather.wind}}</i>
                        <i class="wiv-wind-value">{{currentWeather.windBounce}}</i>
                    </span>
                </div>
            </div>
            <div class="wb-future-weather">
                 <ul class="wfw-listWrap">
                    <li class="lineItem" v-for="item,index in futureWeather">
                        <span class="li-week"><font>{{item.week}}</font></span>
                        <span class="li-weather li-weather-icon">
                            <img :src="require('./images/'+item.img)" class="li-weather-icon" />
                        </span>
                        <span class="li-weather li-weather-text">{{item.text}}</span>
                    </li>
                </ul>
            </div>
             
        </section><!--// end w-body -->

    </div>
</template>
<script scoped>

    import weather, { weatherData }  from './js/weather';
    import toolBrower from 'common/js/tool.browser.js';
    require('./js/jQueryTween-aio-min.js');
    import { TWEEN } from './js/tween.min.js';
    require('./js/jquery.easing.js');

    export default {
        data(){
            return {
                areaInfo: {
                    city: 'CHINA · 深圳',
                    country: '中国 广东省',
                    time: '18:30 pm'
                },
                sweather: {
                    text: '多云转晴',
                    temperature: '27°'
                },
                currentWeather:{
                    tem: '28°',
                    img: 'w1.png',
                    week: 'Monday',
                    day: '15th',
                    wind: '南风 /',
                    windBounce: '3-4级',
                    img: 'w6.png',
                    video: 'https://pic.ibaotu.com/00/47/64/90Y888piCKpf.mp4'
                },
                futureWeather: [{
                    week: 'TUE',
                    img: 'w1.png',
                    text: '25°'
                },{
                    week: 'WED',
                    img: 'w2.png',
                    text: '30°'
                },{
                    week: 'THU',
                    img: 'w3.png',
                    text: '28°'
                },{
                    week: 'FRI',
                    img: 'w4.png',
                    text: '23°'
                },{
                    week: 'SAT',
                    img: 'w5.png',
                    text: '31°'
                }],
                slidersIndex: 0,
                sliders:['weather_bg.jpg','weather_bg1.jpg','weather_bg2.jpg','weather_bg3.jpg','weather_bg4.jpg'],
                slidersOnoff: true,
                slidersTimer: null,
                sIntervTimer: null,
                baseSrc: './images/',
                show: false,
                videoShow: false,
                weatherUrl: 'https://www.sojson.com/open/api/weather/json.shtml?city=深圳',
                hostUrl: 'http://localhost:9093' ,
                weatherDatas: null,
                infosTimer: null,
                infosIndex: 0
            }
        },
        methods:{
            initWeatherData(){

                //记录时间戳检测时间是否需要重新请求数据
                if(!toolBrower.lget('weatherTime') || toolBrower.lget('weatherTime').time < new Date().getDate()){
                    this.$axios.get(this.hostUrl).then((res)=>{
                        this.weatherDatas = JSON.parse(res.data);
                        this.initWeather();
                        var weatherTime = {
                            time: new Date().getDate(),
                            datas: this.weatherDatas
                        };
                        toolBrower.lset('weatherTime',weatherTime);

                    }).catch((err)=>{
                        this.weatherDatas = JSON.parse(res.data);
                        this.initWeather();
                    });
                }else{
                    this.weatherDatas = toolBrower.lget('weatherTime').datas
 ? toolBrower.lget('weatherTime').datas
 : weatherData;
                    this.initWeather();
                }

                //console.log(this.weatherDatas)
            },
            initVideo(){
                /*var v = document.getElementById('videos');
                console.log(v)*/

                $('#videos').get(0).load();
                $('#videos').get(0).volume=0;

                $('#videos').on('loadedmetadata',()=>{
                    console.log('loadedmetadata');
                    $('#videos').get(0).play();
                })


                $('#videos').on('play',(e)=>{
                    this.videoShow = true;
                    
                    //开始执行绘制
                    var canvas = $('#weather-canvas').get(0);
                    var ctx = canvas.getContext('2d');
                    var w = $('.w-header-box').width()+100;
                    var h = $('.w-header-box').height()+100;
                    var data = $('#videos').get(0);

                    function fn(){
                        requestAnimationFrame(fn);
                        ctx.clearRect(0,0,w,h);
                        ctx.drawImage(data,0,0,w,h);
                    };
                    fn();
                });
            },
            initWeather(){

                //当前天气
                this.currentWeather = weather.formtData(this.weatherDatas.data.forecast).c;
                //未来天气
                this.futureWeather = weather.formtData(this.weatherDatas.data.forecast).f;

                this.sweather = weather.formtData(this.weatherDatas.data.forecast).s;

                //地区
                this.areaInfo.city = 'CHINA · '+this.weatherDatas.city;

                this.weather = weather.formtData(this.weatherDatas.data.forecast).f;

                //时间
                this.areaInfo.time = weather.formatTime();

            },
            initEvent(){
                
                //取消图片默认拖拽
                $('img').on('mousedown',function(e){
                    e.stopPropagation();
                    e.preventDefault();
                    return false;
                })

                $('.wh-change-area').on('click',(e)=>{
                    //clearInterval(this.sIntervTimer);
                    this.slidersFunc();
                    e.stopPropagation();
                    return false;
                });

                let positionY = 0;
                var timer = null;
                var fn = ()=>{
                    timer = setTimeout(()=>{
                        requestAnimationFrame(fn);
                        clearTimeout(timer);
                    },3000);

                    this.infosIndex++;
                    positionY = -16 * this.infosIndex;
                    if( this.infosIndex > $('.wp-list-wrap').find('.wli').length-1){
                        this.infosIndex = 0;
                        $('.wp-list-wrap').css({
                            'transition': '0s',
                            'transform':'translateY('+this.infosIndex+'px)'
                        });
                    }else{
                        
                        $('.wp-list-wrap').find('.wli').eq(this.infosIndex).css('opacity','0.9');
                        $('.wp-list-wrap').find('.wli').eq(this.infosIndex+1).css('opacity','0');
                        $('.wp-list-wrap').css({
                            'transition': '.8s',
                            'transform':'translateY('+positionY+'px)'
                        },'swing',()=>{

                        });
                    }
                }

                fn();

            },
            slidersFunc(){
                if(this.slidersOnoff){
                    this.$velocity($('.bgImg'),"stop");
                    this.slidersIndex++;
                    if(this.slidersIndex>=this.sliders.length){
                        this.slidersIndex = 0;
                    }
                    //this.$velocity($('.bgImg').eq(0),'transition.fadeIn',{duration: 1000});
                    this.$velocity($('.bgImg').eq(1),'transition.fadeIn',{duration: 1800, complete:()=>{
                        this.slidersOnoff = true;
                    }});
                    this.slidersOnoff = false;
                }
            },
            //执行收缩动画
            scaleWeather(){

                var cw = 0;
                var left = $('#conainer-weather').parent().css('left');
                var top = $('#conainer-weather').parent().css('top');

                if(this.show){
                    cw = 500;
                    //console.log(jQuery.easing.easeInQuad)
                    $('#conainer-weather').animate({
                        width: cw,
                    },{
                        duration: 800,
                        specialEasing:{ width: 'easeOutBounce' },
                        complete: ()=>{
                            this.$emit('changesize',{w:cw,h:320});
                        }
                    });
                    $('.wh-card-info').css('opacity',1);
                    $('.wfw-listWrap').animate({ opacity: 1 },200);
                    $('.w-header-box').css('transform','scale(1.0) translateX(0px)');
                    $('.sliders').css('filter','blur(0)');
                    this.$velocity($('.li-week'),'transition.slideDownIn', { stagger: 200 , backwards: true});
                    this.$velocity($('.li-weather-icon'),'transition.expandIn', { stagger: 100 });
                    this.$velocity($('.li-weather-text'),'transition.slideUpIn', { stagger: 200 });
                    this.$velocity($('.wci'),'transition.slideUpIn', { stagger: 200 });

                    $('.wh-area').css('transform','translateX(0px)');
                    $('.wh-prompt').css('transform','translateX(0px) translateY(0px)');
                }else{

                    cw = 200;
                    $('#conainer-weather').animate({
                        width: cw
                    },{
                        duration: 800,
                        specialEasing:{ width: 'easeOutBounce' },
                        complete: ()=>{
                            this.$emit('changesize',{w:cw,h:320});
                        }
                    });
                    $('.wh-card-info').css('opacity',0);
                    $('.wfw-listWrap').animate({ opacity: 0 },900);
                    $('.w-header-box').css('transform','scale(1.2) translateX(-30px)');
                    $('.sliders').css('filter','blur(3px)');

                    $('.wh-area').css('transform','translateX(-10px)');
                    $('.wh-prompt').css('transform','translateX(-10px) translateY(20px)');
                }

                this.show = !this.show;
            }
        },
        created(){
            this.initWeatherData();
            
        },
        mounted(){
           this.initEvent();
           this.initVideo();
        }
    }
</script>

<style scoped>
    .time{
        font-size: 14px;
        font-weight: bold;
    }
    .content{
        padding-left: 5px;
    }
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus">
    @import './stylus/weather'          
</style>



